.highlight {
  pre {
    background-color: var(--base00);
  }
  .hll {
    background-color: var(--base00);
  }
  .err {
    color: var(--base0F);
    background-color: var(--base02);
  }

  .gh { } /* Generic Heading & Diff Header */
  .ge { font-style: italic   } /* Generic.Emph */
  .gs { font-weight: bold    } /* Generic.Strong */
  .gu { color: var(--base04) } /* Generic.Subheading & Diff Unified/Comment? */
  .gd { color: var(--base08) } /* Generic.Deleted & Diff Deleted */
  .gi { color: var(--base0B) } /* Generic.Inserted & Diff Inserted */
  .c  { color: var(--base04) } /* Comment */
  .k  { color: var(--base0D) } /* Keyword */
  .l  { color: var(--base0E) } /* Literal */
  .n  { color: var(--base06) } /* Name */
  .o  { color: var(--base08) } /* Operator */
  .p  { color: var(--base06) } /* Punctuation */
  .cm { color: var(--base04) } /* Comment.Multiline */
  .cp { color: var(--base04) } /* Comment.Preproc */
  .c1 { color: var(--base04) } /* Comment.Single */
  .cs { color: var(--base04) } /* Comment.Special */
  .kc { color: var(--base0D) } /* Keyword.Constant */
  .kd { color: var(--base0D) } /* Keyword.Declaration */
  .kn { color: var(--base08) } /* Keyword.Namespace */
  .kp { color: var(--base0D) } /* Keyword.Pseudo */
  .kr { color: var(--base0D) } /* Keyword.Reserved */
  .kt { color: var(--base0D) } /* Keyword.Type */
  .ld { color: var(--base0A) } /* Literal.Date */
  .m  { color: var(--base0E) } /* Literal.Number */
  .s  { color: var(--base0A) } /* Literal.String */
  .na { color: var(--base0B) } /* Name.Attribute */
  .nb { color: var(--base06) } /* Name.Builtin */
  .nc { color: var(--base0B) } /* Name.Class */
  .no { color: var(--base0D) } /* Name.Constant */
  .nd { color: var(--base0B) } /* Name.Decorator */
  .ni { color: var(--base06) } /* Name.Entity */
  .ne { color: var(--base0B) } /* Name.Exception */
  .nf { color: var(--base0B) } /* Name.Function */
  .nl { color: var(--base06) } /* Name.Label */
  .nn { color: var(--base06) } /* Name.Namespace */
  .nx { color: var(--base0B) } /* Name.Other */
  .py { color: var(--base06) } /* Name.Property */
  .nt { color: var(--base08) } /* Name.Tag */
  .nv { color: var(--base06) } /* Name.Variable */
  .ow { color: var(--base08) } /* Operator.Word */
  .w  { color: var(--base06) } /* Text.Whitespace */
  .mf { color: var(--base0E) } /* Literal.Number.Float */
  .mh { color: var(--base0E) } /* Literal.Number.Hex */
  .mi { color: var(--base0E) } /* Literal.Number.Integer */
  .mo { color: var(--base0E) } /* Literal.Number.Oct */
  .sb { color: var(--base0A) } /* Literal.String.Backtick */
  .sc { color: var(--base0A) } /* Literal.String.Char */
  .sd { color: var(--base0A) } /* Literal.String.Doc */
  .s2 { color: var(--base0A) } /* Literal.String.Double */
  .se { color: var(--base0E) } /* Literal.String.Escape */
  .sh { color: var(--base0A) } /* Literal.String.Heredoc */
  .si { color: var(--base0A) } /* Literal.String.Interpol */
  .sx { color: var(--base0A) } /* Literal.String.Other */
  .sr { color: var(--base0A) } /* Literal.String.Regex */
  .s1 { color: var(--base0A) } /* Literal.String.Single */
  .ss { color: var(--base0A) } /* Literal.String.Symbol */
  .bp { color: var(--base06) } /* Name.Builtin.Pseudo */
  .vc { color: var(--base06) } /* Name.Variable.Class */
  .vg { color: var(--base06) } /* Name.Variable.Global */
  .vi { color: var(--base06) } /* Name.Variable.Instance */
  .il { color: var(--base0E) } /* Literal.Number.Integer.Long */
}
